import React, { useState } from "react";
import "./style.css";
import { NavLink } from "react-router-dom";

/**
 * @author
 * @function Header
 **/

const Header = props => {
  const [search, setSearch] = useState(false);

  const submitSearch = e => {
    e.preventDefault();
    alert("123");
  };

  const openSearch = e => {
    search ? setSearch(false) : setSearch(true);
  };

  const searchClass = search ? "searchInput active" : "searchInput";

  return (
    <header className="header">
      <nav className="headerMenu">
        <NavLink
          style={{
            fontSize: "25px",
            fontWeight: "bold",
            color: "black",
            marginLeft: "auto"
          }}
          to="/"
        >
          DevZone
        </NavLink>
        <a href="#">Java</a>
        <a href="#">Python</a>
        <a href="#">HTML</a>
        <a href="#">CSS</a>
        <a href="#">JavaScript</a>
        <a href="#">React</a>
        <a href="#">Android</a>
        <a href="#">Flutter</a>
        <a href="#">Interview</a>
        <a href="#">News</a>
        <a href="#">Jobs</a>
      </nav>
      <div className="search">
        <form onSubmit={submitSearch}>
          <input
            type="text"
            placeholder="Search DevZone"
            className={searchClass}
          />
          <img
            className="searchIcon"
            onClick={openSearch}
            src={require("../../assets/icons/search.png")}
            alt="Search"
          />
        </form>
      </div>
    </header>
  );
};

export default Header;
